.form_control{
    display: flex;
    flex-direction: column;
    margin-bottom: 1em
}

.form_control label{
    margin-bottom: 0.6em;
    font-weight: bold;
    color:#16479d ;
}

.form_control select{
    border: 1px solid #16479d;
    border-radius: 5px;
    padding: 0.7em;
    background-color: #eeff00;
}

