.message{
    width: 60%;
    padding: 1em;
    border: 1px solid #9c9b9b;
    margin: 1.2em auto 0;
    text-align: center;
    border-radius: 5px;
}
.success{
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}

.error{
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}